import React from "react";
import { baseUrl } from "../../../../../utils";

const ImageSkeleton = (props) => {
  return (
    <div className="py-1 text-center">
      <img className="img-fluid" src={baseUrl(`dashboard/analytic/content-analytis-${props.img}.svg`)} width={"100%"} height={"403px"}/>
    </div>
  );
};

export default React.memo(ImageSkeleton)