import React, { Component } from "react";
import ReactApexChart from "react-apexcharts";
import { PREPEND_ZERO } from "../../../../../utils";
class AssetsTypeCountChart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      series: [1, 1, 1],
      options: {
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        stroke: {
          curve: "smooth",
        },
        colors: ["#EF45B1", "#546FED", "#F8B861"],
        plotOptions: {
          pie: {
            donut: {
              stroke: 0,
              legend: {
                show: false,
              },
              labels: {
                show: true,
                total: {
                  label: "Loading...",
                  showAlways: true,
                  show: true,
                },
                value: {
                  show: false,
                },
              },
            },
          },
        },
        chart: {
          type: "donut",
          animations: {
            enabled: true,
            easing: "easeinout",
            speed: 800,
            animateGradually: {
              enabled: true,
              delay: 2000,
            },
            dynamicAnimation: {
              enabled: true,
              speed: 350,
            },
          },
        },
        labels: ["Text", "Image", "Video"],
      },
      imagePost: 0,
      textPost: 0,
      videoPost: 0,
      selectedCollection: [],
    };
  }

  componentDidMount() {
    this.onLoadAnalyticGraph();
  }

  onLoadAnalyticGraph = (data) => {
    const { postTypeCount } = this.props;

    const postCount = [];
    const responseData = {
      total_assets: {
        text: postTypeCount[0].data[0],
        image: postTypeCount[0].data[1],
        video: postTypeCount[0].data[2],
      },
    };

    Object.keys(responseData.total_assets).map((item) => {
      postCount.push(responseData.total_assets[item]);
      this.setState({
        textPost: responseData.total_assets["text"],
        imagePost: responseData.total_assets["image"],
        videoPost: responseData.total_assets["video"],
      });
    });
    this.setState({
      series: postCount,
      options: {
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                total: {
                  label: "Total Assets",
                  showAlways: true,
                  show: true,
                },
                value: {
                  show: true,
                },
              },
            },
          },
        },
      },
    });
  };

  render() {
    const { options, series, imagePost, textPost, videoPost } = this.state;
    return (
      <>
        <ReactApexChart
          options={options}
          series={series}
          type="donut"
          className="assetsType w-100 m-auto"
          style={{ maxWidth: 340 }}
        />
        <div
          className="d-flex px-3 w-100 m-auto mt-4"
          style={{ maxWidth: 450 }}
        >
          <div className="col-4">
            <div
              className="count-box blue text-center"
              style={{ borderTop: "5px solid #EF45B1" }}
            >
              {/* <span className="h-16px w-16px rounded-1 me-2" style={{ backgroundColor: '#EF45B1' }}></span> */}
              <p className="fs-6 fw-medium mb-2">Image Posts</p>
              <span className="text-gray-800 fw-bolder fs-4">
                {PREPEND_ZERO(textPost)}
              </span>
            </div>
          </div>
          <div className="col-4">
            <div
              className="count-box yellow text-center"
              style={{ borderTop: "5px solid #F8B861" }}
            >
              {/* <span className="h-16px w-16px rounded-1 me-2" style={{ backgroundColor: '#546FED' }}></span> */}
              <p className="fs-6 fw-medium mb-2">Video Posts</p>
              <span className="text-gray-800 fw-bolder fs-4">
                {PREPEND_ZERO(imagePost)}
              </span>
            </div>
          </div>
          <div className="col-4">
            {/* <span className="h-16px w-16px rounded-1 me-2" style={{ backgroundColor: '#F8B861' }}></span> */}
            <div
              className="count-box pink text-center"
              style={{ borderTop: "5px solid #546FED" }}
            >
              <p className="fs-6 fw-medium mb-2">Text Posts</p>
              <span className="text-gray-800 fw-bolder fs-4">
                {PREPEND_ZERO(videoPost)}
              </span>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default AssetsTypeCountChart;
